@charset "UTF-8";
/* Mixin */
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Akrobat";
  src: url("../fonts/Akrobat-SemiBold.woff2") format("woff2"), url("../fonts/Akrobat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
/* Box Model */
*,
*::after,
*::before {
  box-sizing: border-box;
}

/* Images */
img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

body {
  position: relative;
}

.scroll-lock {
  overflow: hidden;
}

.wrapper {
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
}
@media (min-width: 640px) {
  .wrapper {
    padding: 0 20px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    max-width: 1440px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.swiper-horizontal > .swiper-scrollbar, .swiper-scrollbar.swiper-scrollbar-horizontal {
  background-color: #9b9b9b;
  bottom: -20px;
  left: 0;
  position: relative;
}

.swiper-scrollbar-drag {
  background-color: #656565;
  width: 84px;
}

.button {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  display: inline-block;
  padding: 12px 18px;
  background-color: #ec6701;
  border-radius: 20px;
  border: 0;
  color: inherit;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
}
@media (any-hover: hover) {
  .button:hover {
    background-color: #9b9b9b;
