}
.button:focus-visible {
  outline: 2px solid rgb(152, 115, 211);
  outline-offset: 3px;
}
.button:active {
  opacity: 0.3;
}

.item-button {
  display: inline-block;
  max-width: 86px;
  margin-top: auto;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4em;
  color: inherit;
  border: 0;
  border-bottom: 1px solid #ffffff;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
  text-align: left;
}
