    display: none;
  }
}

.menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  font-family: "Inter", sans-serif;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out;
  z-index: 3;
  height: 100vh;
}
@media (min-width: 1440px) {
  .menu__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: 131px;
    opacity: 1;
    pointer-events: all;
    max-width: 850px;
    height: unset;
  }
}

.menu--open {
  display: grid;
}

.menu--open .menu__list {
  opacity: 1;
  pointer-events: all;
}

.menu__item {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  background-color: #1e1e1e;
}

.menu__link {
  display: flex;
  justify-content: center;
  text-decoration: none;
  text-transform: uppercase;
  color: inherit;
  padding: 10px;
  width: 100%;
}
@media (min-width: 1440px) {
