  position: relative;
  margin: 0;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: inherit;
}
.footer__address p:not(:last-child) {
  margin-bottom: 10px;
}

.footer__link {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
.footer__link--phone::before {
  content: "";
  position: absolute;
  background-image: url("../images/phone.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 15px;
  left: 1px;
}
.footer__link--mail::before {
  content: "";
  position: absolute;
  background-image: url("../images/mail.svg");
  background-repeat: no-repeat;
  width: 15px;
  height: 11px;
  left: 1px;
  top: 50%;
  transform: translateY(-50%);
}
.footer__link:focus-visible {
  outline: 2px solid rgb(152, 115, 211);
  outline-offset: 3px;
}

.footer__social {
  margin-bottom: 35px;
}
@media (min-width: 640px) {
  .footer__social {
    margin-bottom: 19px;
    grid-column: 2/-1;
    grid-row: 2/2;
  }
}
@media (min-width: 1440px) {
  .footer__social {
    grid-column: 3/3;
    grid-row: -2/2;
    align-self: flex-end;
    padding-left: 22px;
    margin-bottom: 45px;
  }
}

.footer__copy {
  margin: 0;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.4em;
  text-align: left;
  color: inherit;
}
@media (min-width: 640px) {
  .footer__copy {
    margin-bottom: 19px;
  }
}
@media (min-width: 1440px) {
  .footer__copy {
    margin-bottom: 45px;
    grid-column: 1/2;
    align-self: flex-end;
  }
}

@media (min-width: 1440px) {
  .footer__inner .line::before {
    border-left: 2px solid #9b9b9b;
  }
}

.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Inter", sans-serif;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.social__item {
  font-weight: 400;
  font-size: 16px;
}

.social__link {
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
}
@media (any-hover: hover) {
  .social__link:hover {
    opacity: 0.6;
  }
}
.social__link:focus-visible {
  outline: 2px solid rgb(152, 115, 211);
  outline-offset: 3px;
}
.social__link:active {
  opacity: 0.3;
}

.modal {
  z-index: 99;
  position: fixed;
  inset: 0;
  display: grid;
  place-items: center;
  background-color: #1e1e1e;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out;
}
@media (min-width: 1440px) {
  .modal {
    background-color: rgba(0, 0, 0, 0.75);
  }
}

.modal__inner {
  position: relative;
}

.modal .line::after, .modal .line::before {
  z-index: 1;
}

.is-active {
  opacity: 1;
  pointer-events: all;
}

.modal__content {
  padding: 40px;
  overflow: auto;
  height: 100vh;
  background-color: #1e1e1e;
}
@media (min-width: 640px) {
  .modal__content {
    width: 100%;
  }
}
@media (min-width: 1440px) {
  .modal__content {
    width: 1285px;
    max-height: 75vh;
  }
}
@media (min-width: 1440px) {
  .modal__content--description {
    width: 770px;
  }
}

.modal__button {
  padding: 7px;
  background-color: transparent;
  border: none;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
}

.modal__title {
  margin: 0;
  margin-bottom: 16px;
  font-family: "Akrobat", sans-serif;
  font-weight: 600;
  font-size: 35px;
  line-height: 1.1em;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: inherit;
}
@media (min-width: 1440px) {
  .modal__title {
    font-size: 42px;
  }
}

.modal__grid {
  font-family: "Inter", sans-serif;
  display: grid;
}

.modal__info {
  position: relative;
  padding: 20px;
}

.modal__lead {
  font-weight: 500;
  font-size: 15px;
  line-height: 1.2em;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 10px;
  color: inherit;
}
@media (min-width: 1440px) {
  .modal__lead {
    font-size: 20px;
    margin-bottom: 14px;
  }
}
.modal__lead--cases {
  position: relative;
  padding: 20px;
  margin-bottom: 0;
}
.modal__lead--project {
  position: relative;
  padding: 20px;
  margin-bottom: 0;
}

.modal__description {
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.25em;
  margin: 0;
  color: inherit;
  opacity: 0.8;
}
@media (min-width: 1440px) {
  .modal__description {
    font-size: 16px;
    line-height: 1.3em;
    grid-row: 2/2;
    grid-column: 1/2;
  }
}
.modal__description--opacity {
  opacity: 1;
  padding: 20px;
}

.modal__aside {
  display: grid;
}
@media (min-width: 1440px) {
  .modal__aside {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1440px) {
  .modal__aside--big {
    grid-template-columns: repeat(4, 1fr);
  }
}

.modal__list {
  padding: 0;
  padding-left: 10px;
  margin: 0;
  margin-top: 10px;
}

.modal__list li {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.25em;
  color: inherit;
  opacity: 0.8;
}

.modal__aside p {
  position: relative;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.25em;
  color: inherit;
  margin: 0;
  padding: 20px;
}
@media (min-width: 1440px) {
  .modal__aside p {
    margin: -1px;
    font-size: 14px;
  }
}

@media (min-width: 1440px) {
  .modal__grid-cases {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content 1fr;
  }
  .modal__grid-cases:nth-of-type(2n) {
    margin: -2px;
  }
}

@media (min-width: 1440px) {
  .modal__grid-cases h3 {
    grid-column: 1/2;
  }
}

@media (min-width: 1440px) {
  .modal__description-list {
    grid-column: 2/-1;
    grid-row: 2 span;
  }
}

.modal__develop-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 22px;
}

@media (min-width: 1440px) {
  .modal__develop {
    display: flex;
  }
}

.modal__develop dt {
  margin: 0;
  font-weight: 700;
  font-size: 15px;
  line-height: 1.2em;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}

.modal__develop dl {
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.2em;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: inherit;
}

@media (min-width: 1440px) {
  .modal__grid-project {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: min-content 1fr;
  }
}

.modal img {
  margin-top: 10px;
}
@media (min-width: 1440px) {
  .modal img {
    margin-top: 20px;
  }
}

.modal__project-img {
  position: relative;
  padding: 20px;
  margin: -2px;
}
@media (min-width: 1440px) {
  .modal__project-img {
    margin: -2px;
    grid-row: 2 span;
  }
}
.modal__project-img img {
  margin: 0 auto;
  margin-bottom: 10px;
  width: 100%;
}

.modal__description-project {
  padding: 20px;
  margin: -2px;
}
@media (min-width: 1440px) {
  .modal__description-project {
    padding: 40px;
    margin: 0;
  }
}

@media (min-width: 1440px) {
  .modal__description-list-cases {
    margin: -2px;
  }
}

.modal__aside .line:nth-of-type(2n)::before {
  border-left: 2px solid #9b9b9b;
}
@media (min-width: 1440px) {
  .modal__aside .line:nth-of-type(1n)::after {
    border-bottom: 2px solid #9b9b9b;
  }
  .modal__aside .line:nth-of-type(2n)::after {
    border-bottom: 2px solid #9b9b9b;
  }
}

.modal__grid-cases .line:nth-of-type(1)::after {
  border-bottom: 0;
}
.modal__grid-cases .line:nth-of-type(2n)::before {
  border-left: 2px solid #9b9b9b;
}
@media (min-width: 1440px) {
  .modal__grid-cases .line:nth-of-type(1)::after {
    border-bottom: 2px solid #9b9b9b;
  }
}

.modal__grid-project .line:nth-child(3n)::after {
  border-top: 0;
}
@media (min-width: 1440px) {
  .modal__grid-project .line:nth-child(3n)::after {
    border-top: 2px solid #9b9b9b;
  }
  .modal__grid-project .line:nth-child(1)::after {
    border-bottom: 0;
  }
}